import React, {useEffect, useState} from "react";
import style from "./slider.module.css"
import {FaSteam} from "react-icons/fa";
import {NavLink} from "react-router-dom";

const Slider = (props) => {

    let countSlides = props.slidesResult.length;

    return (
        <div>
        <div id="carouselExampleCaptions" className="carousel slide" data-bs-ride="carousel">
            <div className="carousel-inner">

                {props.slidesResult.map(slide => {

                  return  <div className={props.slidesResult.indexOf(slide) === 0 ? `carousel-item active` : `carousel-item`}>

                        <div className="container">
                            <div className="row">
                                <div className="col-lg-6">

                                    <NavLink to={`/product/${slide[0].id}`} className={style.firstTypeSlideBlock}>
                                        <img src={slide[0].imgItem}
                                             className="d-block w-100" alt="..."/>
                                        <div className={style.panelItem}>
                                            <span className={style.platformItem}><FaSteam /></span>
                                            <span className={style.priceItem}>{slide[0].price_rub} ₽</span>
                                        </div>
                                    </NavLink>

                                </div>
                                <div className="col-lg-6">
                                    <div className="row">
                                        <div className="col-6 col-lg-6">

                                            <NavLink to={`/product/${slide[1].id}`} className={style.secondTypeSlideBlock}>
                                                <img
                                                    src={slide[1].imgItem}
                                                    className="d-block w-100" alt="..."/>
                                                <div className={style.panelItem}>
                                                    <div className={style.panelItem}>
                                                        <span className={style.platformItem}><FaSteam /></span>
                                                        <span className={style.priceItem}>{slide[1].price_rub} ₽</span>
                                                    </div>
                                                </div>
                                            </NavLink>

                                            <NavLink to={`/product/${slide[2].id}`} className={style.secondTypeSlideBlock2}>
                                                <img
                                                    src={slide[2].imgItem}
                                                    className="d-block w-100" alt="..."/>
                                                <div className={style.panelItem}>
                                                    <div className={style.panelItem}>
                                                        <span className={style.platformItem}><FaSteam /></span>
                                                        <span className={style.priceItem}>{slide[2].price_rub} ₽</span>
                                                    </div>
                                                </div>
                                            </NavLink>


                                        </div>
                                        <div className="col-6 col-lg-6">


                                            <NavLink to={`/product/${slide[3].id}`} className={style.secondTypeSlideBlock}>
                                                <img
                                                    src={slide[3].imgItem}
                                                    className="d-block w-100" alt="..."/>
                                                <div className={style.panelItem}>
                                                    <div className={style.panelItem}>
                                                        <span className={style.platformItem}><FaSteam /></span>
                                                        <span className={style.priceItem}>{slide[3].price_rub} ₽</span>
                                                    </div>
                                                </div>
                                            </NavLink>


                                            <NavLink to={`/product/${slide[4].id}`} className={style.secondTypeSlideBlock2}>
                                                <img
                                                    src={slide[4].imgItem}
                                                    className="d-block w-100" alt="..."/>
                                                <div className={style.panelItem}>
                                                    <div className={style.panelItem}>
                                                        <span className={style.platformItem}><FaSteam /></span>
                                                        <span className={style.priceItem}>{slide[4].price_rub} ₽</span>
                                                    </div>
                                                </div>
                                            </NavLink>

                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                })}




            </div>

            <div className={style.panelSlideItems}>
                <div className="carousel-indicators">
                    <button type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide-to="0" className="active"
                            aria-current="true" aria-label="Slide 1"></button>
                    <button type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide-to="1"
                            aria-label="Slide 2"></button>
                    <button type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide-to="2"
                            aria-label="Slide 3"></button>
                </div>
            </div>
        </div>


    </div>

    )
}
export default Slider;