import React, {useEffect, useState} from "react";
import Slider from "../slider/sliderContainer";
import style from "./main.module.css";
import Items from "./itemsMainBlock/itemsContainer"
const Main = (props) => {

    let [initSlider, setInitSlider] = useState(props.initSlider)

    useEffect(() => {
        setInitSlider(props.initSlider)
        console.log(initSlider)
    }, [initSlider, props.initSlider])

    return (

        <div style={{
            padding: 0,
            background: "#000"
        }} className="container-lg center">
            {initSlider && <Slider />}
            <br />
            <div className="container">
                <div className="row">
                    <div className="col-lg">
                        <span className={style.menuItem}>Лидеры продаж</span>
                    </div>
                    <div className="col-lg">
                        <span className={style.menuItem}>Рекомендуем</span>
                    </div>
                    <div className="col-lg">
                        <span className={style.menuItem}>Скидки</span>
                    </div>
                </div>
            </div>

            <Items />

            </div>

    )
}

export default Main;